.erro {
    min-width: 500px;
    min-height: 50px;
    position: fixed;
    background: #e1dede;
    z-index: 100;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-bottom: 10px solid #ff3b3b;
    border-bottom-right-radius: 30px;
    border-bottom-left-radius: 30px;
    display: none;
    align-items: center;
    justify-content: center;
    box-shadow: 1px 1px 20px 13px #522a2a;
    animation-name: ani;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    padding: 10px;
}
svg.x22 {
    position: absolute;
    top: -25px;
    right: -10px;
    background: #000000;
    fill: red;
    z-index: 10000;
    width: 30px;
    height: 30px;
    cursor: pointer;
}
@keyframes ani {
    0%{
        border-bottom: 10px solid #ff3b3b;

    }
    100%{
        border-bottom: 10px solid #e1dede;

    }
}


