.list-section {
    display: flex;
    width: 100%;
    height: 100vh;
    background-position: center;
    background-color: black;
    background-image: url("./org file/background-list-page1.png");
    background-size: cover;
    align-items: center;
    justify-content: start;
    flex-direction: column;
    box-sizing: border-box;
    padding-top: 35px;
}

.list-section-1 {
    height: 1px;
    width: 500px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 25x;
}

.list-section-1-search {
    background-color: rgba(217, 217, 217, 0.13);
    width: 100%;
    height: 30px;
    border-radius: 5px;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
}
.list-section-1-sorth {
    height: 20px;
    width: 100%;
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row-reverse;
}
.list-section-1-length {
    width: 100%;
    height: 20px;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.list-section-1-lists {
    overflow: auto;
    width: 100%;
    margin-top: 10px;
    flex-grow: 1;
    background-image: linear-gradient(to top, #5050500f, #bdbdbd2e);
    height: 300px;
}
.list-section-1-lists::-webkit-scrollbar{
    background-color: rgba(182, 182, 182, 0.062);
    width: 7px;
}
.list-section-1-lists::-webkit-scrollbar-thumb{
    background-color: #858585b4;
    border-radius: 10px;
}
.list-section-1-lists::-webkit-scrollbar-button{
width: 100%;
height: 5px;
}
  


.list-section-1-search-bottom {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(217, 217, 217, 0.38);
    height: 100%;
    width: 30px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
.list-section-1-search-camerascan {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 5px;
    width: 30px;
}
input.list-section-1-search-input {
    flex-grow: 1;
    height: 100%;
    border: none;
    background: transparent;
    text-align: right;
    padding-right: 10px;
}

p.list-section-1-sorth-test {
