.list-section-1-list-c {
    position: absolute;
    left: 0px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #0d61a6;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    width: 55px;
}
svg.list-section-1-list-d {
    position: absolute;
    right: 7px;
    top: 7px;
}
