    font-family: sans-serif;
    background: linear-gradient(to right,#0D61A6 ,#BFBFBF );
    -webkit-background-clip: text;
    color: transparent;
}

@media (max-width: 480px) {

    .list-section {
        width: 100%;
        height: 100vh;
        background-position: center;
        background-size: cover;
        align-items: center;
        justify-content: center;
        position: relative;
        padding-top: 35px;
        padding-bottom: 40px;
        background-image: radial-gradient(#212020, #0E0D0D);
    }
    .list-section-1 {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: static;
        top: auto;
        left: auto;
        transform: none;
        justify-content: start;
        height: 100%;
    }
    .list-section-2 {
    margin-top: 0px;
    height: 7%;
    }
    .list-section-2-text {
        font-size: 25px;
        font-weight: bold;
        font-family: sans-serif;
        background: linear-gradient(to right,#0D61A6 ,#BFBFBF );
        -webkit-background-clip: text;
        color: transparent;
    }
    .list-section-1-search {
        width: 95%;
    }
    .list-section-1-sorth {
        margin-top: 15px;
        flex-direction: column;
    }
    .list-section-1-sorth-listinput {
        margin-top: 5px;
    }
    p.list-section-1-sorth-test {
           font-size: 12px;
    }
    .list-section-1-lists {
        width: 95%;
    }



}