.bill-section{
    display: flex;
    width: 100%;
    height: 100%;
    background-position: center;
    background-color: black;
    background-image: url("./org file/background-bill-page1.png");
background-size:cover;
align-items: start;
justify-content: center;
position: relative;

}
.filly{
   fill:"#b4b4b4"
}
.bill-section-1 {
    width: 700px;
    height: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50% ,-50%);

}
.bill-section-2{
margin-top: 35px;
width: 100%;
height: 10%;
display: flex;
align-items: center;
justify-content: center;
}
.bill-section-1-a {
    width: 100%;
    height: 350px;
    background-color: #5b5b5b29;
    border-radius: 20px;
    backdrop-filter: blur(5px);
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
}
.bill-section-1-a-1 {
    height: 100%;
    width: 57%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.bill-section-1-a-2 {
    height: 100%;
    flex-grow: 1;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: #0000002e;
    direction: rtl;
    overflow: auto;
}
.bill-section-1-a-2-a {
    overflow: auto;
    width: 97%;
    height: 100%;
}
.bill-section-1-a-2-a::-webkit-scrollbar{
    width: 6px;
    background-color: rgba(177, 177, 177, 0.212);
}
.bill-section-1-a-2-a::-webkit-scrollbar-thumb{
border-radius: 4px;  
  background-color: rgba(177, 177, 177, 0.63);
}
.bill-section-1-a-2-a::-webkit-scrollbar-button{
    height: 6px;
}
.bill-section-1-b{
    overflow: hidden; 
    width: 95%;
    height: 50px;
}
.bill-section-2-text{
    color: aliceblue;
    font-size: 30px;
    font-weight: bold;
    font-family: sans-serif;
    background: linear-gradient(to right,#0D61A6 ,#BFBFBF );
    -webkit-background-clip: text;
    color: transparent;
}
.bill-section-1-a-1-a {
    background-color: #bdbdbd2e;
    width: 350px;
    height: 200px;
    border-radius: 20px;
    margin-top: 16px;
    backdrop-filter: blur(10px);
    box-shadow: inset 1px 2px 20px 1px rgba(0, 0, 0, 0.33);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.bill-section-1-a-1-b {
    display: flex;
    flex-direction: column;
    width: 350px;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
}
.bill-section-1-a-2-a-1 {
    width: 98%;
    height: 40px;
    background-color: rgba(197, 197, 197, 0.58);
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 5px;
}
.bill-section-1-a-2-a-1-a {
    width: 90%;
    display: flex;
    height: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.bill-section-1-a-2-a-1-b {
    flex-grow: 1;
    height: 100%;
    background-color: #d25757;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
}
p.bill-section-1-a-2-a-1-a-1 {
    font-size: 9px;
    font-family: sans-serif;
    color: black;
}
p.bill-section-1-a-2-a-1-a-2 {
    font-weight: bold;
    color: rgba(46, 45, 45, 1);
    font-size: 16px;
    font-family: sans-serif;
}
p.bill-section-1-a-2-a-1-a-3 {
    font-size: 11px;
    font-family: sans-serif;
    background-color: rgba(189, 189, 189, 1);
    padding: 0px 10px;
}
.bill-section-1-a-1-b-2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.bill-section-1-a-1-b-2-r {
    display: none;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.bill-section-1-a-1-b-2-- {
    display: none;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.bill-section-1-a-1-b-1 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 20px;
}.bill-section-1-a-1-b-2-b {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
}
p.bill-section-1-a-1-b-1-a {
    font-family: sans-serif;
    font-weight: bold;
    font-size: 25px;
    color: #bcbcbc;
}
p.bill-section-1-a-1-b-1-b {
    color: #d6d6d6;
    font-size: 12px;
    margin-top: 5px;
    font-family: sans-serif;
}
button.bill-section-1-a-1-b-2-a {
    width: 100px;
    height: 30px;
    color: white;
    background-color: rgba(210, 87, 87, 1);
    border: none;
    border-radius: 5px;
    font-size: 17px;
}
button.bill-section-1-a-1-b-2-c {
    width: 100px;
    height: 30px;
    color: white;
    background-color: rgba(13, 97, 166, 1);
    border: none;
    border-radius: 5px;
    font-size: 17px;
}
button.bill-section-1-a-1-b-2-d-1 {
    width: 45px;
    height: 16px;
    font-size: 11px;
    font-family: sans-serif;
}input.bill-section-1-a-1-b-2-d-2 {
    box-sizing: border-box;
    width: 70px;
    text-align: center;
    background-color: rgba(217, 217, 217, 0.15);
    border: solid 0.4px #303030;
    margin-bottom: 6px;
}
@media (max-width: 480px) {
    .bill-section-2 {
        margin-top: 35px;
        width: 100%;
        height: 10%;
        display: none;
        align-items: center;
        justify-content: center;
    }
    .bill-section {
        width: 100%;
        height: 100%;
        background-position: center;
        background-size: cover;
        align-items: start;
        justify-content: center;
        position: relative;
        padding-top: 35px;
        padding-bottom: 40px;
        background-image: radial-gradient(#212020, #0E0D0D);
        box-sizing: border-box;
    }
    .bill-section-1 {
        width: 98%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: static;
        top: 55%;
        left: 50%;
        transform: none;
    }
    .bill-section-1-a {
        width: 100%;
        height: 100%;
        background-color: #5b5b5b00;
        border-radius: 20px;
        backdrop-filter: blur(5px);
        display: flex;
        justify-content: start;
        flex-direction: column;
        align-items: center;
    }
    .bill-section-1-a-1 {
        height: auto;
        width: 94%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: start;
    }
    .bill-section-1-a-1-a {
        background-color: #bdbdbd2e;
        width: 100%;
        height: 200px;
        border-radius: 20px;
        margin-top: 16px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .bill-section-1-a-1-b {
        margin-bottom: 0px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 70px;
    }
    .bill-section-1-a-1-b-2 {
     display:none;
    }
    .bill-section-1-a-2 {
        height: 50%;
        flex-grow: 1;
        width: 100%;
        margin-bottom: 10px;
    }
    .bill-section-1-a-2-a {
        margin-bottom: 10px;
    }
    .bill-section-1-b{
   display: none;
    }
    .bill-section-1-a-1-b-2-b {
        flex-direction: row-reverse;
    }
    input.bill-section-1-a-1-b-2-d-2 {
        margin-bottom: 0px;
    }
    .bill-section-1-a-1-b-1 {
        margin-bottom: 0px;
    }
    .bill-section-1-a-1-b-2-r {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 90%;
        direction: ltr;
    }
}
#video-scan{
   height: 100%;
    display: none;
}
.camera-section-svg2 {
    width: 150px;
}
.cam-buttom {
    background-color: #d9d9d952;
    height: 25px;
    border-radius: 5px;
    width: 130px;
    box-shadow: inset black 0px 0px 6px 1px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
    border: none;
}
.camera-section2 {
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
}